import * as React from "react";
import { RadialChart, Hint } from "react-vis";
import { colors } from "../../../design-system";
import { isEmpty } from "lodash";
import {
  totalParticipants,
  gender,
} from "../../../content/ambassadors-report/metrics";

interface Props {}
const Income: React.FC<Props> = () => {
  const [value, setValue]: any = React.useState({});

  return (
    <div>
      <p>Income Metrics</p>
      <RadialChart
        colorRange={[colors.greyDark, colors.blueDark, colors.blue]}
        animation
        data={gender}
        width={300}
        height={300}
        showLabels
        onValueMouseOver={(v: any) => {
          setValue({
            data: v,
            label: gender.find(item => item.label === v.label).label,
            total:
              totalParticipants *
              (gender.find(item => item.label === v.label).angle * 0.01),
            percentage: gender.find(item => item.label === v.label).angle,
          });
        }}
        onSeriesMouseOut={(v: any) => setValue({})}
      >
        {!isEmpty(value) && (
          <Hint value={value.data}>
            <div style={{ background: "black" }}>
              <h3>{value.label}</h3>
              <p>{value.total}</p>
            </div>
          </Hint>
        )}
      </RadialChart>
    </div>
  );
};

export default Income;
